.top-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100vh;

  .intro {
    text-align: right;
    width: 60%;
    padding-right: 4em;
    border-right: 1px solid var(--text-primary);

    h1 {
      font-size: 8rem;
      font-weight: bold;
      line-height: 0.8em;
      margin: 0;
    }

    p {
      font-size: 2em;
      margin: 0;
    }
  }

  .signature {
    text-align: center;
    width: 60%;
    height: 100%;
    padding-left: 4em;
    img {
      max-width: 100%;
      max-height: 350px;
      margin-top: -175px;
      top: 50%;
      position: relative;
    }
    svg {
      height: 100%;
      width: 100%;

      .signature-path {
        // Got this value by logging path.getTotalLength()
        stroke-dasharray: 640;
        stroke-dashoffset: 640;
        animation: drawSignature 5s linear forwards;
      }
    }
  }

  .social {
    position: absolute;
    bottom: 3em;
    margin: 0 auto;
  }
}

@media screen and (max-width: 1300px) {
  .top-section {
    .intro {
      width: 50%;

      h1 {
        font-size: 7rem;
      }
    }
    .social {
      position: absolute;
      bottom: -1em;
      margin: 0 auto;
    }
  }
}

@media screen and (max-width: 768px) {
  .top-section {
    flex-direction: column;

    .intro {
      text-align: center;
      border-bottom: 1px solid var(--text-primary);
      border-right: none;
      width: auto;
      padding: 0;
      padding-bottom: 2em;

      h1 {
        font-size: 5rem;
      }
    }

    .signature {
      height: auto;
      padding: 0;
      padding-top: 2em;
      img {
        max-width: 100%;
        max-height: 250px;
        margin-top: 0;
        top: 0;
        position: relative;
      }
    }
  }
}

@keyframes drawSignature {
  to {
    stroke-dashoffset: 0;
  }
}
